<template>
  <div id="app">
    <Heading msg="commissions i'm waiting on..." />
    <a href="https://ref.basicallyodd.net"
      ><p style="font-style: italic;">
        View Completed Art of My Characters Here (Click)
      </p></a
    >
    <br />
    <div class="commgroup">
      <table class="commtable">
        <thead>
          <tr>
            <th class="commdata">Commission</th>
            <th class="commdata">Artist</th>
            <th class="commdata">Date of Commission</th>
            <th class="commdata">Date of Payment</th>
            <th class="commdata">Status</th>
            <th class="commdata">Time Since Start of Commission</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="commission of commissions"
            v-bind:key="commission.commission_id"
            v-bind:id="commission.commission_id"
            class="comm"
          >
            <td class="commdata">
              <a :href="'/timeline/' + commission.commission_id"
              v-tooltip="'Click to view timeline'">{{
                commission.commission_name
              }}</a>
            </td>
            <td class="commdata">
              <a v-bind:href="commission.detail_artist_link" target="_blank">{{
                commission.detail_artist
              }}</a>
            </td>
            <td class="commdata">
              {{
                (commission.detail_date_of_commission / 1)
                  | moment("dddd, MMMM Do YYYY, h:mm:ss a")
              }}
            </td>
            <td class="commdata">
              {{
                (commission.detail_date_of_completed_payment / 1)
                  | moment("dddd, MMMM Do YYYY, h:mm:ss a")
              }}
            </td>
            <td class="commdata">
              <a
                v-bind:href="commission.latest_status_link"
                target="_blank"
                v-bind:class="commission.state_css"
                >{{ commission.latest_status }}</a
              >
            </td>
            <td class="commdata">
              {{ commission | timecalc }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import Heading from "./components/Heading.vue";
import json from "./json/data.json";

/* eslint-disable */

var now = Math.floor(new Date().getTime() / 1000);

export default {
  name: "app",
  components: {
    Heading
  },
  data() {
    return {
      commissions: json,
      time: now
    };
  },
  computed: {
    activeComms: function() {
      return this.commissions.filter(function(u) {
        return !u.state_complete;
      });
    }
  },
  filters: {
    timesince: function(date) {
      var seconds = parseInt(now - date, 10);
      var days = Math.floor(seconds / (3600 * 24));
      seconds -= days * 3600 * 24;
      var hrs = Math.floor(seconds / 3600);
      seconds -= hrs * 3600;
      var mnts = Math.floor(seconds / 60);
      seconds -= mnts * 60;
      return (
        days + " days, " + hrs + " hours, " + mnts + " minutes" //, " +
        // seconds +
        // " seconds"
      );
    },
    timecalc: function(comm) {
      if (comm.latest_status === "Done") {
        var date = comm.updates[comm.updates.length - 1].timestamp;
        var seconds = parseInt(
          date - comm.detail_date_of_completed_payment,
          10
        );
        var days = Math.floor(seconds / (3600 * 24));
        seconds -= days * 3600 * 24;
        var hrs = Math.floor(seconds / 3600);
        seconds -= hrs * 3600;
        var mnts = Math.floor(seconds / 60);
        seconds -= mnts * 60;
        return (
          "Received finished piece after " +
          days +
          " days, " +
          hrs +
          " hours, " +
          mnts +
          " minutes" //, " +
          // seconds +
          // " seconds"
        );
      } else {
        var date = comm.detail_date_of_completed_payment;
        var seconds = parseInt(now - date, 10);
        var days = Math.floor(seconds / (3600 * 24));
        seconds -= days * 3600 * 24;
        var hrs = Math.floor(seconds / 3600);
        seconds -= hrs * 3600;
        var mnts = Math.floor(seconds / 60);
        seconds -= mnts * 60;
        return (
          "Waiting for " +
          days +
          " days, " +
          hrs +
          " hours, " +
          mnts +
          " minutes" //, " +
          // seconds +
          // " seconds"
        );
      }
    }
  }
};
</script>

